}
.title {
 font-size: 2.5rem;
}
* {
  color: #fff;
}
.padding-0 {
  padding: 0;
}
.card-item {
  width: 270px;
  height: 450px;
  margin-left: 1em;
  margin-right: 1em;
    margin-bottom: 4em;
}
.card-itemE {
  width: 270px;
  margin-left: 1em;
  margin-right: 1em;
    margin-bottom: 4em;
}
.col-div {
  box-shadow: #fff 5
}
.fondo {
